<template>
  <div class="inner__view" >
    <p class="chart-title">今日付款订单数实时变化</p>
    <div id="chart"  class="base-chart"></div>
  </div>
</template>
<script>

import chartsMixins from './../mixins/index.js'
export default {
  mixins: [chartsMixins],
  data() {
    return {
      el: 'chart',
      optionsData: {
        grid: {
          top: 10,
          left: 40,
          right: 0
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: false
          },
          data: ['00-00', '00-00', '00-00', '00-00', '00-00', '00-00', '00-00']

        },
        yAxis: {
          type: 'value',
          axisLine: {
            show: false
          },
          splitLine: {
            lineStyle: {
              type: 'dotted'
            }
          }
        },
        series: [{
          data: [0, 0, 0, 0, 0, 0, 0],
          type: 'line',
          smooth: true
        }]
      }

    }
  },
  props: {
    optionData: {
      type: Object
    }
  },
  watch: {
    optionData: {
      handler(oldVal, newVal) {
        this.changeData(this.optionData)
      },
      deep: true
    }
  },
  methods: {
    changeData(data) {
      this.optionsData.xAxis.data = data.option.xAxis
      this.optionsData.series[0].data = data.option.yAxis
      this.chart.setOption(this.optionsData)
    }
  }
}
</script>

<style scoped>
.chart-title{
  font-size: 14px;
  margin-bottom: 10px;
  color: #666;
}
.inner__view{
  padding: 10px 15px;
  height: 201px;
  border-right: 1px solid #f0f0f0;
   border-bottom: 1px solid #f0f0f0;
}
.base-chart{
  width: 100%;
  height: 180px;
}
</style>



