<template>
  <!-- 商品看板 -->
  <div class="mt-20">
    <div class="stat__header">
      <div class="fl">店铺看板
   

      </div>
      <div class="fr">
        <router-link to="/statistics/store" :style="{color:color}">查看店铺分析</router-link>
      </div>
    </div>
    <div class="stat__body">
      <el-row>
        <el-col :span="24"  v-loading="loading">
          <div id="storeBarChart" class="flow-chart"></div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import { storeIntroduce } from './../../../../../../api/statistics/index.js'
import chartsMixins from './../mixins/index.js'
export default {
  mixins: [chartsMixins],
  data() {
    return {
      optionsData: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: '2%',
          right: '2%',
          bottom: 30,
          top: 30,
          containLabel: true
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: false
          },
          data: ['三星', '华为', '苹果', '小米', '锤子', '一加'],
          axisTick: {
            alignWithLabel: true
          }
        },

        yAxis: {
          splitLine: {
            show: false
          },
          type: 'value'
        },

        series: [
          {
            type: 'bar',
            barMaxWidth: 25,
            barCategoryGap: '20%',
            data: [10, 52, 200, 334, 390, 330, 220]
          }
        ]
      },
      el: 'storeBarChart'
    }
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    getData() {
      this.loading = true
      storeIntroduce(this.queryParams).then(res => {
        if (res.code === 200) {
          this.optionsData.xAxis.data = res.data.xAxis
          this.optionsData.series[0].data = res.data.yAxis
          this.chart.setOption(this.optionsData)
        }
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>





