<template>
  <div class="border-left" >
    <el-radio-group v-model="value" size="mini" style="margin:20px">
       <el-radio-button label="销售额排行"></el-radio-button>
      <el-radio-button label="销量排行"></el-radio-button>
    </el-radio-group>
    <sale-chart v-if="value==='销售额排行'"></sale-chart>
    <count-chart  v-else></count-chart>
  </div>
</template>
<script>
import saleChart from './components/saleChart'
import countChart from './components/countChart'
export default {
  components: {
    saleChart,
    countChart
  },
  data() {
    return {
      value: '销售额排行'
    }
  }

}
</script>





