<template>
<div v-loading="loading">
    <div id="proBarChart" class="flow-chart"></div>
</div>
</template>
<script>

import { statisticsProductPayAmount } from './../../../../../../../api/statistics/index.js'
import chartsMixins from './../../mixins/index.js'
export default {
  mixins: [chartsMixins],
  data() {
    return {
      value: '销售额排行',
      el: 'proBarChart',
      optionsData: {
        grid: {
          top: 0,
          left: 20,
          right: 20,
          bottom: 20,
          containLabel: true
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        xAxis: {
          splitLine: {
            show: false
          },
          type: 'value',
          boundaryGap: [0, 0.01]
        },
        yAxis: {
          splitLine: {
            show: false
          },
          type: 'category',
          data: []
        },
        series: [
          {
            type: 'bar',
            barMaxWidth: 25,
            barCategoryGap: '10%',
            data: [9000, 6000, 2000, 1000, 200, 100]
          }
        ]
      }
    }
  },
  methods: {
    getData() {
      this.loading = true
      statisticsProductPayAmount(this.queryParams).then(res => {
        if (res.code === 200) {
          this.optionsData.series[0].data = res.data.values
          this.optionsData.yAxis.data = res.data.names
          this.chart.setOption(this.optionsData)
        }
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>

<style scoped lang="scss">
.flow-chart {
  width: 100%;
  height: 300px;
}

</style>



