<template>
  <div>
    <table class="stat-table">
      <tr>
        <td class="stat-table__header">今日</td>
        <td class="stat-table__info">
          <p class="stat-table__title">付款订单数</p>
          <count-to :endVal="today.orders" class="count-val"></count-to>
        </td>
        <td class="stat-table__info">
          <p class="stat-table__title">用户数</p>
          <count-to :endVal="today.members" class="count-val"></count-to>
        </td>
        <td class="stat-table__info">
          <p class="stat-table__title">商品数</p>
          <count-to :endVal="today.products" class="count-val"></count-to>
        </td>
        <td class="stat-table__info">
          <p class="stat-table__title">店铺数</p>
          <count-to :endVal="today.stores" class="count-val"></count-to>
        </td>
      </tr>
      <tr>
        <td class="stat-table__header">昨日</td>
        <td class="stat-table__info">
          <p class="stat-table__title">付款订单数</p>
          <div class="count-warp">
            <count-to :endVal="yesterday.orders" class="count-val"></count-to>
            <div class="count-prenct"  :class="getStateClass( yesterday.orderPrecent)">
            <img :src="getState(yesterday.orderPrecent)" alt="">
              <count-to :endVal="Math.abs(yesterday.orderPrecent)"  suffix="%"></count-to>
            </div>
          </div>
        </td>
        <td class="stat-table__info">
          <p class="stat-table__title">用户数</p>
         <div class="count-warp">
            <count-to :endVal="yesterday.members" class="count-val"></count-to>
            <div class="count-prenct" :class="getStateClass(yesterday.memberPrecent)">
             <img :src="getState(yesterday.memberPrecent)" alt="">
              <count-to :endVal="Math.abs(yesterday.memberPrecent)" suffix="%"></count-to>
            </div>
          </div>
        </td>
        <td class="stat-table__info">
          <p class="stat-table__title">商品数</p>
          <div class="count-warp">
            <count-to :endVal="yesterday.products" class="count-val"></count-to>
            <div class="count-prenct" :class="getStateClass(yesterday.productPrecent)">
              <img :src="getState(yesterday.productPrecent)" alt="">
              <count-to :endVal="Math.abs(yesterday.productPrecent)"  suffix="%"></count-to>
            </div>
          </div>
        </td>
        <td class="stat-table__info">
          <p class="stat-table__title">店铺数</p>
          <div class="count-warp">
            <count-to :endVal="yesterday.stores" class="count-val"></count-to>
            <div class="count-prenct"  :class="getStateClass(yesterday.storePrecent)">
                 <img :src="getState(yesterday.storePrecent)" alt="">
              <count-to :endVal="Math.abs(yesterday.storePrecent)" suffix="%"></count-to>
            </div>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import countTo from 'vue-count-to'
const upImg = require('./../../../../../../assets/images/s-up.png')
const downImg = require('./../../../../../../assets/images/s-down.png')
export default {
  components: {
    countTo
  },
  props: {
    optionData: {
      type: Object
    }
  },
  data() {
    return {
      today: {},
      yesterday: {}
    }
  },
  watch: {
    optionData: {
      handler(oldVal, newVal) {
        this.changeData(this.optionData)
      },
      deep: true
    }
  },
  methods: {
    getStateClass(val) {
      if (val === 0) {
        return ''
      } else if (val > 0) {
        return 'up'
      } else {
        return 'down'
      }
    },
    getState(val) {
      if (val === 0) {
        return null
      } else if (val > 0) {
        return upImg
      } else {
        return downImg
      }
    },
    changeData(data) {
      this.today = data.today
      this.yesterday = data.yesterday
    }
  },
  filters: {
    getState(val) {
      if (val === 0) {
        return '-'
      } else if (val > 0) {
        return '↑'
      } else {
        return '↓'
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.count-prenct{
  img{
    vertical-align: top;
    width: 12px;
    height: 12px;
  }
}
</style>



