<template>
  <div class="mt-20">
    <div class="stat__header">
      <div class="fl">会员看板
        <el-tooltip placement="right" effect="light">
          <div slot="content">累计总数： 累计注册用户的总数
            <br>新增数：新增注册用户数
            <br>活跃数：登录用户总数
            <br>成交会员数：成功下单且支付的会员总数
            <br>复购用户数：非第一次下单且支付的用户总数
            <br>新购用户数：第一次下单且支付的用户总数
            <br>复购率：复购用户数 / 总下单用户数
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <div class="fr">
        <router-link to="/statistics/member" :style="{color:color}">查看会员分析</router-link>
      </div>
    </div>
    <div class="stat__body">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="14" :xl="14">
          <line-chart></line-chart>
        </el-col>
        <el-col :xs="24" :sm="24" :md="24" :lg="10" :xl="10">
          <pie-chart></pie-chart>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import lineChart from './lineChart'
import pieChart from './pieChart'
export default {
  components: {
    lineChart,
    pieChart
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  data() {
    return {}
  }
}
</script>





