<template>
  <div class="border-left" v-loading="loading">
    <div id="memberPieChart" class="flow-chart"></div>
  </div>
</template>
<script>
import { memberStatistics } from './../../../../../../api/statistics/index.js'
import chartsMixins from './../mixins/index.js'
export default {
  mixins: [chartsMixins],
  data() {
    return {
      el: 'memberPieChart',
      optionsData: {
        title: {
          text: '复购率',
          subtext: '75%',
          left: 'center',
          top: 10
        },

        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          bottom: 40,
          right: 30,
          data: ['复购用户数', '新购用户数']
        },
        series: [
          {
            name: '复购率',
            type: 'pie',
            radius: ['45%', '70%'],
            center: ['50%', '55%'],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              }
            },
            labelLine: {
              normal: {
                show: false
              }},
            data: [
              { value: 0, name: '复购用户数' },
              { value: 0, name: '新购用户数' }
            ]
          }
        ]
      }
    }
  },
  methods: {
    getData() {
      this.loading = true
      memberStatistics(this.queryParams).then(res => {
        if (res.code === 200) {
          this.optionsData.series[0].data = [
            { value: res.data.newStatistics.usedDealMember, name: '复购用户数' },
            { value: res.data.newStatistics.newDealMember, name: '新购用户数' }
          ]
          this.optionsData.title.subtext = res.data.newStatistics.usedDealProportion + '%'
          this.chart.setOption(this.optionsData)
        }
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>

<style scoped lang="scss">
.flow-chart {
  width: 100%;
  height: 372px;
}

</style>



