<template>
 <cms-sticky fixClass="tag__warpper--fixed">
  <div >
    <div class="stat__header" style="padding:5px 15px;">
      <div class="fl" style="line-height:28px">看板视图</div>
      <div class="fr">
        <el-date-picker
          v-model="value2"
          @change="getTime"
          value-format="yyyy-MM-dd"
          align="right"
          type="date"
          placeholder="选择日期"
          :editable="false"
          :picker-options="pickerOptions1"
           :clearable="false"
        ></el-date-picker>
      </div>
    </div>
  </div>
 </cms-sticky>
</template>

<script>
 const moment = require('moment')
export default {
 
   data() {
     return {
       pickerOptions1: {
         disabledDate(time) {
           return time.getTime() > (Date.now() - 86400000)
         },
         shortcuts: [
           {
             text: '今天',
             onClick(picker) {
               picker.$emit('pick', new Date())
             }
           },
           {
             text: '昨天',
             onClick(picker) {
               const date = new Date()
               date.setTime(date.getTime() - 3600 * 1000 * 24)
               picker.$emit('pick', date)
             }
           },
           {
             text: '一周前',
             onClick(picker) {
               const date = new Date()
               date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
               picker.$emit('pick', date)
             }
           }
         ]
       },
       value2: moment().subtract(1, 'days').format('YYYY-MM-DD')
     }
   },
   watch: {
     value2() {
       if (this.value2 === '') {
         this.value2 = moment().subtract(1, 'days').format('YYYY-MM-DD')
       }
     }
   },
   methods: {
     getTime(time) {
       this.$store.dispatch('setPanelDate', time)
     }
   }
}
</script>

<style>
</style>



