<template>
  <!-- 商品看板 -->
  <div class="mt-20">
    <div class="stat__header">
      <div class="fl">商品看板
        <el-tooltip placement="right" effect="light">
          <div slot="content">在售商品数： 平台所有已上架的商品总数
            <br>被访问商品总数：商品详情页面的总次数（单个商品访问多次做去重处理）
            <br>动销量：已出售的商品总数量
            <br>动销率：已出售的商品总数 / 在售商品的总库存
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <div class="fr">
        <router-link to="/statistics/product" :style="{color:color}">查看商品分析</router-link>
      </div>
    </div>
    <div class="stat__body">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="14" :xl="14">
          <line-chart></line-chart>
        </el-col>
        <el-col :xs="24" :sm="24" :md="24" :lg="10" :xl="10">
          <chart-warp></chart-warp>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import lineChart from './lineChart'
import chartWarp from './chartWarp'
export default {
  components: {
    lineChart,
    chartWarp
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  data() {
    return {}
  }
}
</script>





