import echarts from 'echarts'
export default {
  data() {
    return {
      chart: null, // 图表实例
      el: null, // 挂载的图标标识
      optionsData: {},
      loading: false
    }
  },
  computed: {
    queryParams() {
      return {
        endTime: this.$store.state.statistics.panelDate
      }
    }

  },
  watch: {
    queryParams: {
      handler() {
        this.getData()
      },
      deep: true
    }

  },
  methods: {
    getData() { }, // 只要复写这个函数就可以操作对应的图表数据,
    initChart() { // 初始化图表
      this.$nextTick(() => {
        this.chart = echarts.init(document.getElementById(this.el), 'walden')
      })
    }

  },
  created() {
    this.getData()
  },
  mounted() { // 监听图表
    this.initChart()
    window.addEventListener('resize', () => {
      this.chart.resize()
    })
  }

}



