<template>
  <div class="mt-20">
    <div class="stat__header">
      <div class="fl">流量看板
        <el-tooltip placement="right" effect="light">
          <div slot="content">
          访客数（UV）： 访问站点的用户数，单个用户访问多次记一次<br>
          页面流量（PV）：访问站点页面次数，同一用户访问多次计算多次<br>
          人均流量：页面流量 / 访客数<br>
          付款订单数：成功下单且付款的订单数，统一下单按拆单后数量计算<br>
          跳出率：指用户访问一次页面后退出行为的访客数占所有访客数的比率<br>
          商品访问转化率：访问商品详情页面次数占页面流量的比率<br>
          访客加购率：加入购物车行为的访客数（相同户多次行为只计算一次）占所有访客数的比率<br>
          访客支付转化率：下单且支付行为的访客数（相同用户多次行为只计算一次） 占所有访客数的比率<br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <div class="fr">
        <router-link  to="/statistics/pv" :style="{color:color}">查看流量分析</router-link>
      </div>
    </div>
    <div class="stat__body">
     <chart></chart>
    </div>
  </div>
</template>
<script>
import chart from './chart.vue'
export default {
  components: {
    chart
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  data() {
    return {
    }
  }
}
</script>







