<template>
  <div>
    <div class="stat__header">
      <div class="fl">平台概况
        <el-tooltip placement="right" effect="light">
          <div slot="content">付款订单数：成功下单且付款的订单数，统一下单按拆单后数量计算（今日数指0点~实时）
            <br>用户数：成功注册的会员数（今日指数指0点~实时）
            <br>商品数：在售的商品总数，组合SKU按一个计算（今日指数指0点~实时）
            <br>店铺数：已开通的店铺数，包含禁用及暂停营业（今日指数指0点~实时）
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
    </div>
    <div class="stat__body">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="14">
          <count-chart v-loading="loading" :option-data="chartData"></count-chart>
        </el-col>
        <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="10">
          <count-table v-loading="loading" :option-data="chartData"></count-table>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import { statisticsIntroduce } from './../../../../../../api/statistics/index.js'
import chartsMixins from './../mixins/index.js'
import countChart from './countChat.vue'
import countTable from './countTable'
export default {
  mixins: [chartsMixins],
  components: {
    countChart,
    countTable
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  data() {
    return {
      chartData: {}
    }
  },
  methods: {
    getData() {
      this.loading = true
      statisticsIntroduce(this.queryParams).then(res => {
        if (res.code === 200) {
          this.chartData = res.data
        }
      }).finally(() => {
        this.loading = false
      })
    },
    initChart() {
      return false
    }
  }
}
</script>





