<template>
  <div v-loading="loading">
    <ul class="stat-count__warp">
      <li class="stat-count__item">
        <p>累计总数</p>
        <count-to :endVal="countMember"></count-to>
      </li>
      <li class="stat-count__item">
        <p>新增数</p>
        <count-to :endVal="newMember"></count-to>
      </li>
      <li class="stat-count__item">
        <p>活跃数</p>
        <count-to :endVal="visitMember"></count-to>
      </li>
      <li class="stat-count__item">
        <p>成交会员数</p>
        <count-to :endVal="dealMember"></count-to>
      </li>
    </ul>
    <div id="memberLineChart" class="flow-chart"></div>
  </div>
</template>
<script>
import { memberStatistics } from './../../../../../../api/statistics/index.js'
import chartsMixins from './../mixins/index.js'
import countTo from 'vue-count-to'
export default {
  mixins: [chartsMixins],
  components: {
    countTo
  },
  data() {
    return {
      el: 'memberLineChart',
      optionsData: {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['累积总数', '新增数', '活跃数', '成交会员数'],
          left: 30,
          top: 20
        },
        grid: {
          top: 60,
          left: 30,
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: true,
          splitLine: {
            show: false,
            lineStyle: {
              type: 'dashed'
            }
          },
          data: ['00-00', '0-0', '0-0']
        },
        yAxis: [
          {
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                type: 'dotted'
              }
            },
            type: 'value'
          },
          {
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                type: 'dotted'
              }
            },
            type: 'value',
            axisLabel: {
              formatter: value => {
                var texts = []
                const obj = value * 100
                texts.push(obj + '%')
                return texts
              }
            }
          }
        ],
        series: [
          {
            name: '累积总数',
            type: 'line',
            smooth: true,
            data: []
          },
          {
            name: '新增数',
            type: 'line',
            smooth: true,
            data: []
          },
          {
            name: '活跃数',
            type: 'line',
            smooth: true,
            data: []
          }, {
            name: '成交会员数',
            type: 'line',
            smooth: true,
            data: []
          }
        ]
      },
      countMember: 0,
      newMember: 0,
      visitMember: 0,
      dealMember: 0
    }
  },
  methods: {
    getData() {
      this.loading = true
      memberStatistics(this.queryParams).then(res => {
        if (res.code === 200) {
          this.optionsData.xAxis.data = res.data.statisticsDate
          this.optionsData.series[0].data = res.data.countMember// 累积总数
          this.optionsData.series[1].data = res.data.newMember// 新增数
          this.optionsData.series[2].data = res.data.visitMember// 活跃数
          this.optionsData.series[3].data = res.data.dealMember// 成交会员数
          this.chart.setOption(this.optionsData)
          this.countMember = res.data.countMember.reverse()[0]
          this.newMember = res.data.newMember.reverse()[0]
          this.visitMember = res.data.visitMember.reverse()[0]
          this.dealMember = res.data.dealMember.reverse()[0]
        }
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>
<style scoped lang="scss">
.flow-chart {
  width: 100%;
  height: 300px;
}
.stat-count__warp{
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.stat-count__item{
  float: left;
  font-size: 14px;
  color: #666;
  padding:  0 20px;
  text-align: center;
  p{
    margin: 20px 0 15px 0;
  }
  span{
    font-size: 16px;
    font-weight: bold;
  }
}
</style>



