<template>
<!-- 流量看板数据 -->
  <div v-loading="loading">
    <div id="proLineChart" class="flow-chart"></div>
  </div>
</template>
<script>

import { statisticsProductTotalInfo } from './../../../../../../api/statistics/index.js'
import chartsMixins from './../mixins/index.js'
export default {
  mixins: [chartsMixins],
  data() {
    return {
      el: 'proLineChart',
      optionsData: {
        title: {
          text: '商品趋势',
          top: 20,
          left: 30,
          textStyle: {
            fontSize: 12,
            fontWeight: 'normal',
            color: '#999'
          }
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['在售商品数', '被访问商品数', '动销量', '动销率'],
          left: 120,
          top: 20

        },
        grid: {
          top: 70,
          left: 20,
          right: 20,
          bottom: 20,
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: true,
          splitLine: {
            show: false,
            lineStyle: {
              type: 'dashed'
            }
          },
          data: ['0-0']
        },
        yAxis: [
          {
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            },
            type: 'value'
          },
          {
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            },
            type: 'value',
            axisLabel: {
              formatter: (value) => {
                var texts = []
                const obj = value
                texts.push(obj + '%')
                return texts
              }
            }
          }
        ],
        series: [
          {
            name: '在售商品数',
            type: 'line',
            smooth: true,
            data: [0]
          }
        ]
      }
    }
  },
  methods: {
    getData() {
      this.loading = true
      statisticsProductTotalInfo(this.queryParams).then(res => {
        if (res.code === 200) {
          const series = []
          res.data.datas.forEach(v => { // 这里修改配置项
            const obj = {
              name: v.name,
              data: v.data,
              type: 'line',
              smooth: true,
              yAxisIndex: v.name === '动销率' ? 1 : 0
            }
            series.push(obj)
          })
          this.optionsData.series = series
          this.optionsData.xAxis.data = res.data.dateStrs
          this.optionsData.legend.data = res.data.datas.map(v => v.name)
          this.optionsData.legend.data = res.data.datas.map(v => v.name)
          this.chart.setOption(this.optionsData)
        }
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>

<style>
.flow-chart {
  width: 100%;
  height: 368px;
}
</style>



