<template>
  <!-- 流量看板数据 -->
  <div v-loading="loading">
    <swiper :options="swiperOption" ref="mySwiper" class="pv-swiper">
      <swiper-slide
        class="pv-slide"
        v-for="(item,index) in swiperItems"
        :key="index"
        :style="activeSwiperItem.indexOf(item)>-1
                  ?{borderColor:color}:{}"
        @click.native="changeActiveItem(item)"
      >
        <p>{{item}}</p>
        <span class="count-val">{{swiperData[index].newData}}</span>
        <div :class="getStateClass(swiperData[index].newData,swiperData[index].oldData)">
          <span>
            {{swiperData[index].name.indexOf('率')>-1
            ?"上一日:":'较上一日'
            }}
          </span>
          <span>
            {{swiperData[index].oldData}}
            <em
              v-if="swiperData[index].scale"
            >({{swiperData[index].scale}})</em>
          </span>
        </div>
      </swiper-slide>
    </swiper>
    <div>
      <div class="swiper-button-prev" slot="button-prev"></div>
      <div class="swiper-button-next" slot="button-next"></div>
    </div>
    <div id="flowChart" class="flow-chart"></div>
  </div>
</template>
<script>
// const upImg = require('../../../../../../../assets/images/s-up.png')
// const downImg = require('../../../../../../../assets/images/s-down.png')
import { statisticsFluxView } from './../../../../../../api/statistics/index.js'
import chartsMixins from './../mixins/index.js'
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  mixins: [chartsMixins],
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      optionsData: {
        title: {
          text: '最多显示四项',
          top: 20,
          left: 30,
          textStyle: {
            fontSize: 12,
            fontWeight: 'normal',
            color: '#999'
          }
        },
        tooltip: {
          trigger: 'axis',
          formatter: function(datas) {
            let str = `${datas[0].axisValue}<br/>`
            datas.forEach(v => {
              let tmp = ''
              if (v.seriesName.indexOf('率') > -1) {
                tmp = `${v.marker} ${v.seriesName}:${v.data}%<br/>`
              } else {
                tmp = `${v.marker} ${v.seriesName}:${v.data}<br/>`
              }
              str += tmp
            })
            return str
          }
        },
        legend: {
          data: ['访客数', '页面流量', '跳出率', '商品访问转化率'],
          left: 120,
          top: 20
        },
        grid: {
          top: 70,
          left: 30,
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          splitLine: {
            show: false
          },
          data: ['10-01', '10-02', '10-03', '10-04', '10-05', '10-06', '10-07']
        },
        yAxis: [
          {
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            },
            type: 'value'
          },
          {
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                show: false
              }
            },
            type: 'value',
            axisLabel: {
              formatter: value => {
                var texts = []
                const obj = value
                texts.push(obj + '%')
                return texts
              }
            }
          }
        ],
        series: [
          {
            name: '访客数',
            type: 'line',

            smooth: true,
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: '页面流量',
            type: 'line',

            smooth: true,
            yAxisIndex: 1,
            data: [0.1, 0.2, 0.5, 0.4, 0.5, 0.3, 0.5]
          },
          {
            name: '跳出率',
            type: 'line',
            smooth: true,
            data: [150, 232, 201, 154, 190, 330, 410]
          },
          {
            name: '商品访问转化率',
            type: 'line',

            smooth: true,
            data: [320, 332, 301, 334, 390, 330, 320]
          }
        ]
      },
      el: 'flowChart',
      swiperOption: {
        slidesPerView: 4,
        spaceBetween: 20,
        slidesPerGroup: 4,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      swiperItems: [],
      swiperData: [],
      chartData: [],
      activeSwiperItem: [
        '访客数(UV)',
        '页面流量(PV)',
        '跳出率',
        '商品访问转换率'
      ]
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper
    },
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    getStateClass(newData, oldData) {
      const nData = parseInt(newData)
      const oData = parseInt(oldData)
      if (nData !== 0 && oData !== 0) {
        return nData > oData ? 'up' : 'down'
      }
    },
    getData() {
      this.loading = true
      statisticsFluxView(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.chartData = res.data.chartData
            this.optionsData.legend.data = this.activeSwiperItem
            this.optionsData.series = this.changeLine(this.activeSwiperItem)
            this.optionsData.xAxis.data = res.data.showTime
            this.swiperItems = res.data.showName
            this.swiperData = res.data.showData
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    changeActiveItem(item) {
      const index = this.activeSwiperItem.indexOf(item)
      if (index > -1) {
        this.activeSwiperItem.splice(index, 1)
      } else {
        this.activeSwiperItem.push(item)
      }
      this.optionsData.series = Object.assign(
        [],
        this.changeLine(this.activeSwiperItem)
      )
      this.chart.setOption(this.optionsData, true)
    },
    changeLine(datas) {
      const series = []
      this.chartData.forEach(v => {
        // 这里修改配置项
        const obj = {
          name: v.name,
          data: v.data,
          type: 'line',
          smooth: true,
          yAxisIndex: v.name.indexOf('率') > -1 ? 1 : 0
        }
        if (datas.indexOf(v.name) > -1) {
          series.push(obj)
        }
      })
      return series
    }
  }
}
</script>

<style lang="scss" scoped>
.flow-chart {
  width: 100%;
  height: 300px;
}
.pv-slide {
  min-height: 70px;
  border: 1px solid #f0f0f0f0;
  box-sizing: border-box;
  padding: 15px;
  font-size: 14px;
  color: #666;
  cursor: pointer;
}
.pv-swiper {
  width: 85%;
  margin: 15px auto;
}
.swiper-button-prev,
.swiper-button-next {
  top: 55px;
}
.swiper-button-prev,
.swiper-container-rtl .swiper-button-next {
  left: 5%;
}
.swiper-button-next,
.swiper-container-rtl .swiper-button-prev {
  right: 5%;
}
em{
  font-style: normal;
}
</style>



